import "./addCompany.css";
import { useEffect, useState } from 'react';

function AddCompany(): JSX.Element {
    const [jwt,setJWT] = useState("");
    useEffect(() => {
        setJWT(localStorage.getItem("jwt"));
    })
    
    return (
        <div className="addCompany">
		<h1> </h1> <hr />
        my token is: <br />
        {jwt}
        </div>
    );
}

export default AddCompany;
