import "./login.css";
import axios from 'axios';
import { Button, ButtonGroup, Checkbox, FormControlLabel, TextField, Typography } from "@mui/material";
import { Send, Cancel } from "@mui/icons-material";
import UserCred from '../../../modal/userCred';
import Message from "../../../modal/message";
import { useForm } from "react-hook-form";
import { useState } from "react";
import notify from '../../util/notify';
import advNotify, { ErrMsg, SccMsg } from "../../util/notify_advanced";
import globals from "../../util/global";



function Login(): JSX.Element {
    const {register, handleSubmit, formState:{errors}} = useForm<UserCred>();
    const [jwt,setJwt] = useState("no token");
    //let url = "http://localhost:8080/login";

    async function send(msg: UserCred) {
      let myData:any;
        try {
            //url += "userEmail="+msg.userEmail+"&userPassword="+msg.userPassword+"&userType="+msg.userType;
            //const response = await axios.post(url,msg);
            const response = await axios.post(globals.urls.Login,msg);
            setJwt(response.data);
            console.log(response.status);
            notify.success(SccMsg.LOGIN_APPROVED);
            localStorage.setItem("jwt", response.data);
        } catch(err) {
            console.log(err);
           // notify.success("Something went wrong! bad user or email");
           advNotify.error(ErrMsg.LOGIN_ERR);
        }
 
    }


    return (
        <div className="login SolidBox">
			<Typography variant="h3" className="HeadLine" > </Typography><hr /><br />
            <form onSubmit={handleSubmit(send)}>
            <TextField name="userEmail" label="" variant="outlined" fullWidth {...register("userEmail",{
               required: {
                 value: true,
                 message: '   '
              }
            })}/>
            <span>{errors.userEmail?.message}</span>
            <br />
            <br /><br />
            <TextField type={"password"} name="userPassword" label="" variant="outlined" fullWidth {...register("userPassword",{
                required: {
                    value: true,
                    message: '  '
                  }
            })}/>
           {errors.userPassword && <span> </span>}
           <br />
            <br /><br />
            <TextField name="userType" label=" " variant="outlined" fullWidth {...register("userType",{
                required: {
                     value: true,
                     message: '   '
           }
            })}/>
            <br />
          {errors.userType && <span>message error : {errors.userType.message}</span>}
            <br /><br />


          {/*<FormControlLabel name="showNotifications" label=" " control={<Checkbox/>} {...register("remmemberMe")}/>*/}

            <br />
            <ButtonGroup variant="contained" fullWidth>
                <Button type="submit" color="primary"> </Button>
                {/*<Button type="reset" color="secondary" startIcon={<Cancel/>}>Reset</Button>*/}
            </ButtonGroup>
            <br />
            {/*jwt*/}
            </form>
        </div>
    );
}

export default Login;


